
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Input, DatePicker } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import service from '../../services/offer';
import ProductService from '../../services/product';
import UserService from '../../services/userList';
import Pagination from '../components/Pagination';
import UploadImage from '../../utils/UploadImage';
import { AntdMsg } from '../../utils/Antd';
import dayjs from 'dayjs';
import moment from 'moment';


export default function Offer() {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, });
    const [product, setProduct] = useState([]);
    const [user, setUser] = useState([]);
    const addNewModalRef = useRef();
    const columns = [
        {
            title: '#',
            width: 30,
            render: (v, row, i) => <b>{i + 1}</b>
        },
        {
            title: 'Title',
            dataIndex: 'title',
            width: 150
        },

        // {
        //     title: 'Discount Type',
        //     dataIndex: 'discountType',
        //     width: 100
        // },
        {
            title: 'Discount Percent',
            dataIndex: 'discountPercent',
            width: 100
        },
        {
            title: 'Max Discount',
            dataIndex: 'maxDiscountValue',
            width: 100
        },
        {
            title: 'Min Order Price',
            dataIndex: 'minimunOrderPrice',
            width: 100
        },
        {
            title: 'Duration',
            dataIndex: 'startDate',
            width: 200,
            render: (v, row) => <span>{moment(v).format('DD MMM YYYY')} To {moment(row.endDate).format('DD MMM YYYY')} </span>
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this User?"
                        onConfirm={() => { deleteData(row._id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        setLoading(true);
        service.list(qData).then(res => {
            setData(res.data?.map((v) => ({ key: v._id, ...v })).reverse());
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    function listProduct() {
        ProductService.listUser()
            .then(res => { setProduct(res.data || []); }).catch(err => { AntdMsg('Product data not loaded', 'error'); });
    }
    function listUser() {
        UserService.listUser()
            .then(res => { setUser(res.data || []); }).catch(err => { AntdMsg('User data not loaded', 'error'); });
    }

    const deleteData = (id) => {
        service.delete(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
        listProduct()
        listUser()
    }, [qData.page, qData.limit]);


    return (
        <>
            <Typography.Title level={5} style={{ marginTop: 0 }}>List of Offer</Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list, product, user }} />
        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, }) {

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col xs={24} xl={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name or code" value={qData.key} onChange={e => (setQData({ ...qData, key: e.target.value }))} allowClear />
                    </Form.Item>
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected images?. Be sure to delete the image from server."
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }
                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list, product, user } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [file, setFile] = useState([]);


    const handleChange = (value) => {

        Object.entries(value).forEach(ent => {
            let varDt = data;
            const k = ent[0];
            const v = ent[1];
            let keys = k.split('.');
            for (let i = 0; i < keys.length; i++) {
                if (i + 1 === keys.length) {
                    varDt[keys[i]] = v;
                } else {
                    if (typeof varDt[keys[i]] === 'undefined') {
                        if (parseInt(keys[i + 1]) * 1 >= 0) {
                            varDt[keys[i]] = [];
                        } else {
                            varDt[keys[i]] = {};
                        }
                    } varDt = varDt[keys[i]];
                }
            }
        });
        setData({ ...data });
    }


    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : { fileType: 'photo' }
            );
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        service.details(data._id).then(res => {
            setData(res.data || {});
            setFile(res.data.imgs.length ? [...res.data.imgs] : [])
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        setAjxRequesting(true);
        service.save(data).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            list();
            setOpen(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });

    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        }
    }, [open]);

    useEffect(() => {
        console.log('data', data);
    }, [data]);

    useEffect(() => {
        setData({ ...data, imgs: file })
    }, [file])



    return (
        <>

            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Offer'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={500}
                className="app-modal-body"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>

                            <Col span={12}>
                                <Form.Item label="Title" required>
                                    <Input placeholder="Title" value={data.title} onChange={e => { handleChange({ title: e.target.value }) }} />
                                </Form.Item>
                            </Col>

                            <Col span={12}>
                                <Form.Item label="Discount Percent" required>
                                    <Input placeholder="Discount Percent" value={data.discountPercent} onChange={e => { handleChange({ discountPercent: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Max Discount Value" required>
                                    <Input placeholder="Max Discount Amount" value={data.maxDiscountValue} onChange={e => { handleChange({ maxDiscountValue: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Minimun Order Value" required>
                                    <Input placeholder=" Minimun Order Amount" value={data.minimunOrderPrice} onChange={e => { handleChange({ minimunOrderPrice: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            {/* <Col span={24}>
                                <Form.Item label="Discount Type" required>
                                    <Select value={data.discountType} onChange={e => { handleChange({ discountType: e }) }} >
                                        <Select.Option value={'percent'}>Percent</Select.Option>
                                        <Select.Option value={'flat'}>Flat</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col> */}
                            <Col span={24}>
                                <Form.Item label="Product" required>
                                    <Select mode="multiple" value={data.productIds} onChange={v => { handleChange({ productIds: v }) }}
                                        options={
                                            product.map(v => (
                                                {
                                                    value: v._id,
                                                    label: v.name,
                                                }
                                            ))
                                        } >
                                    </Select>
                                </Form.Item>
                            </Col>

                            <Col span={24}>
                                <Form.Item label="Duration" required>
                                    <DatePicker.RangePicker
                                        format='DD MMM YYYY'
                                        value={data.startDate && data.endDate && [dayjs(data.startDate), dayjs(data.endDate)]}
                                        onChange={(e) => { handleChange({ startDate: e?.[0] ? new Date(e?.[0]) : null, endDate: e?.[1] ? new Date(e?.[1]) : null }) }}
                                        style={{ width: '100%' }}
                                    />
                                </Form.Item>
                            </Col>

                            {/* <Col span={24}>
                                <Form.Item label="User" required>
                                    <Select mode='multiple' value={data.userIds} onChange={v => { handleChange({ userIds: v }) }}
                                        options={
                                            user.map(v => (
                                                {
                                                    value: v._id,
                                                    label: v.name,
                                                }
                                            ))
                                        } >
                                    </Select>
                                </Form.Item>
                            </Col> */}
                            {/* <Col span={24}>
                                <Form.Item label="Description">
                                    <Input.TextArea placeholder="Description" value={data.description} onChange={e => { handleChange({ description: e.target.value }) }} />
                                </Form.Item>
                            </Col> */}
                            {/* <Col span={24}>
                                <Form.Item label="Term Conditions">
                                    <Input.TextArea placeholder="Term Condition" value={data.termCondition} onChange={e => { handleChange({ termCondition: e.target.value }) }} />
                                </Form.Item>
                            </Col> */}
                            {/* <Col span={24}>
                                <Form.Item label="Status" required>
                                    <Select value={data.status} onChange={e => { handleChange({ status: e }) }} >
                                        <Select.Option value={true}>Active</Select.Option>
                                        <Select.Option value={false}>Inactive</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col> */}
                            <Col span={20}>
                                <Form.Item label="Photo">
                                    <UploadImage {...{ fileList: file, setFileList: setFile, count: 20 }} />
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});