
/* eslint-disable react-hooks/exhaustive-deps */

import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Input } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import Pagination from '../../components/Pagination';
import { AntdMsg } from '../../../utils/Antd';
import util from '../../../utils/util';
import service from '../../../services/variant';

export default function Varient({ productId }) {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);

    // const [qData, setQData] = useState({ page: 1, limit: 20, productId });

    const [qData, setQData] = useState({ page: 1, limit: 20, productId, });
    const addNewModalRef = useRef();
    const addNewLabelRef = useRef();
    const columns = [
        {
            title: '#',
            width: 40,
            render: (v, row, i) => <b>{i + 1}</b>
        },
        {
            title: 'Slug',
            dataIndex: 'slug',
            width: 100,
        },
        {
            title: 'Category',
            dataIndex: 'cateId',
            width: 100,
        },
        {
            title: 'Color',
            dataIndex: 'color',
            width: 100,
        },
        {
            title: 'Inventory',
            dataIndex: 'inventory',
            width: 100,
        },
        {
            title: 'Strile',
            dataIndex: 'strile',
            width: 100,
        },
        {
            title: 'Total',
            dataIndex: 'total',
            width: 100,
        },
        {
            title: 'Unit',
            dataIndex: 'unite',
            width: 100,
        },
        {
            title: 'Volume',
            dataIndex: 'volume',
        },
        {
            title: 'Price',
            dataIndex: 'price',
            width: 100,
        },
        {
            title: 'Kilo-Gram',
            dataIndex: 'kg',
            width: 100,
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this data?"
                        onConfirm={() => { deleteData(row._id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);
    function list() {
        setLoading(true);
        service.listUser(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }
    const deleteData = (id) => {
        service.deleteUser(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }
    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));
    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';
    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };
    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);
    return (
        <>
            {!productId ? <Typography.Title level={5} style={{ marginTop: 0 }}>List of Varient</Typography.Title> : null}
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list, productId }} />
            <AddLabel ref={addNewLabelRef} {...{ list }} />
        </>
    );
};
function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, }) {
    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name or code" value={qData.key} onChange={e => (setQData({ ...qData, key: e.target.value }))} allowClear />
                    </Form.Item>
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected data ?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }
                </Col>
            </Row>
        </Form>
    );
};
const AddForm = forwardRef((props, ref) => {
    const { list, productId } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);

    const handleChange = (value) => {
        setData({ ...data, ...value });
    }
    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : { productId }
            );
        }
    }));
    const getDetails = () => {
        setAjxRequesting(true);
        service.detailUser(data._id).then(res => {
            setData(res.data || {});
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }
    const save = () => {
        setAjxRequesting(true);
        service.saveUser(data).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            list();
            setOpen(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });
    }
    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        }
    }, [open]);
    useEffect(() => {
        if (!data._id) {
            handleChange({ slug: util.removeSpecialChars(data.name || "") });
        }
    }, [data.name]);
    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Varient'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={500}
                className="app-modal-body"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={24}>

                                <Form.Item label="Category" required>
                                    <Input placeholder="Category" value={data.cateId} onChange={e => { handleChange({ cateId: e.target.value }) }} />
                                </Form.Item>
                                <Form.Item label="Price" required>
                                    <Input placeholder="price" value={data.price} onChange={e => { handleChange({ price: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Slug" required>
                                    <Input placeholder="Slug" value={data.slug} onChange={e => { handleChange({ slug: util.removeSpecialChars(e.target.value) }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>

                                <Form.Item label="Color" required>
                                    <Input placeholder="Color" value={data.color} onChange={e => { handleChange({ color: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>

                                <Form.Item label="Kilo-Gram" required>
                                    <Input placeholder="Kilo-Gram" value={data.kg} onChange={e => { handleChange({ kg: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            {/* <Col span={24}>

                                <Form.Item label="Inventory" required>
                                    <Input placeholder="Inventory" value={data.inventory} onChange={e => { handleChange({ inventory: util.removeSpecialChars(e.target.value) }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Strile" required>
                                    <Input placeholder="Strile" value={data.strile} onChange={e => { handleChange({ strile: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Total" required>
                                    <Input placeholder="Total" value={data.total} onChange={e => { handleChange({ total: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Unit" required>
                                    <Input placeholder="Unit" value={data.unite} onChange={e => { handleChange({ unite: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Volume" required>
                                    <Input placeholder="Volume" value={data.volume} onChange={e => { handleChange({ volume: e.target.value }) }} />
                                </Form.Item>

                            </Col>

                            </Col> */}

                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});


const AddLabel = forwardRef((props, ref) => {
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData({ ...dt });
        }
    }));

    return (
        <>
            <Modal
                title={(!data._id ? 'Add User' : 'List of Varient')}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={1200}
                className="app-modal-body"
            >
                <Varient productId={data._id} />
            </Modal>
        </>
    );
});
