import axios from "../utils/axios";

export default class service {

    static baseURL = '';




    static list(data) {
        return axios.get("coupon/list", { params: data });
    }
    static save(data, query) {
        return axios.post("coupon/save", data, { params: query });
    }

    static delete(id) {
        return axios.post('coupon/delete', { ids: id });
    }

    static details(id) {
        return axios.get('coupon/details/' + id);
    }


}