import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Input, Tag, DatePicker } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';

import service from '../../services/coupon';
import UserService from '../../services/userList';
import Pagination from '../components/Pagination';
import { AntdMsg } from '../../utils/Antd';
import dayjs from 'dayjs';
import moment from 'moment';

export default function Coupon() {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, });
    const [user, setUser] = useState([]);
    const addNewModalRef = useRef();
    const columns = [
        {
            title: '#',
            width: 30,
            render: (v, row, i) => <b>{i + 1}</b>
        },
        {
            title: 'Title',
            width: 150,
            dataIndex: 'title',

        },

        {
            title: 'Code',
            width: 100,
            dataIndex: 'code',

        },
        {
            title: 'EndDate',
            width: 100,
            dataIndex: 'expiryDate',

        },
        {
            title: 'Discount Value',
            width: 100,
            dataIndex: 'discountValue',

        },
        {
            title: 'Coupon Use',
            width: 100,
            dataIndex: 'typeOfUse',

        },



        {
            title: 'Status',
            dataIndex: 'status',
            width: 60,
            render: isActive => {
                if (isActive) {
                    return <Tag color='green'>Active</Tag>
                } else {
                    return <Tag color='red'>Inactive</Tag>
                }
            },
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this User?"
                        onConfirm={() => { deleteData(row._id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        setLoading(true);
        service.list(qData).then(res => {
            setData(res.data?.map((v) => ({ key: v._id, ...v })).reverse());
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    function listUser() {
        UserService.listUser()
            .then(res => { setUser(res.data || []); }).catch(err => { AntdMsg('User data not loaded', 'error'); });
    }

    const deleteData = (id) => {
        service.delete(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));
    console.log(tableColumns)
    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
        listUser()
    }, [qData.page, qData.limit]);



    return (
        <>
            <Typography.Title level={5} style={{ marginTop: 0 }}>List of Coupon</Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list,user }} />
        </>
    );
};



function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, }) {
    const [data, setData] = useState([])


    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined) {
            qData[key] = "";
            list()
        }

    }

    useEffect(() => {
        service.list().then((res) => {
            setData(res.data?.map((e) => ({ label: e.title, value: e._id })))
        })
        // setData(res.data.map((e) => ({ label: e.title, value: e._id })))


    }, [])

    console.log(data)
    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                {/* <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name or code" value={qData.key} onChange={e => (setQData({ ...qData, key: e.target.value }))} allowClear />
                    </Form.Item>
                </Col> */}

                <Col xs={24} xl={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Select
                            showSearch
                            allowClear
                            placeholder="Select an Account"
                            onChange={e => (setQData({ ...qData, _id: e, title: data[0].label }))}
                            //     optionFilterProp="children"
                            // filterOption={(input, option) =>
                            //     (option?.label ?? '').toLowerCase().includes(input.toLowerCase())
                            // }
                            // value={data.value}
                            options={data}
                        />




                    </Form.Item>

                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected images?. Be sure to delete the image from server."
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }
                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list,user } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [fileList, setFileList] = useState([]);


    const handleChange = (value) => {

        Object.entries(value).forEach(ent => {
            let varDt = data;
            const k = ent[0];
            const v = ent[1];
            let keys = k.split('.');
            for (let i = 0; i < keys.length; i++) {
                if (i + 1 === keys.length) {
                    varDt[keys[i]] = v;
                } else {
                    if (typeof varDt[keys[i]] === 'undefined') {
                        if (parseInt(keys[i + 1]) * 1 >= 0) {
                            varDt[keys[i]] = [];
                        } else {
                            varDt[keys[i]] = {};
                        }
                    } varDt = varDt[keys[i]];
                }
            }
        });
        setData({ ...data });

    }


    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : { fileType: 'photo' }
            );
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        service.details(data._id).then(res => {
            setData(res.data || {});
            setFileList(res.data?.avatar ? [res.data?.avatar] : []);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        setAjxRequesting(true);
        service.save(data).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            list();
            setOpen(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });

    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        } else {
            setFileList([]);
        }
    }, [open]);

    // useEffect(() => {
    //     console.log('data', data);
    // }, [data]);



    return (
        <>

            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Coupon'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={500}
                className="app-modal-body"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>

                            <Col span={24}>
                                <Form.Item label="Title" required>
                                    <Input placeholder="Title" value={data.title} onChange={e => { handleChange({ title: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Max DiscountValue" required>
                                    <Input placeholder="Max DiscountValue" value={data.maxDiscountValue} onChange={e => { handleChange({ maxDiscountValue: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Minimun Order Value" required>
                                    <Input placeholder=" Minimun Order Amount" value={data.minimunOrderPrice} onChange={e => { handleChange({ minimunOrderPrice: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Discount Type" required>
                                    <Select value={data.discountType} onChange={e => { handleChange({ discountType: e }) }} >
                                        <Select.Option value={'percent'}>Percent</Select.Option>
                                        <Select.Option value={'flat'}>Flat</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                            <Form.Item label="Code" required>
                                    <Input placeholder="Code" value={data.code} onChange={e => { handleChange({ code: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Expiry Date" required>
                                    <DatePicker format='DD MMM YYYY' onChange={(e) => { handleChange({ expiryDate: e }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Discount Value" required>
                                    <Input placeholder="discountValue" value={data.discountValue} onChange={e => { handleChange({ discountValue: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                             <Col span={24}>
                                <Form.Item label="User" >
                                    <Select mode='multiple' value={data.userIds} onChange={v => { handleChange({ userIds: v }) }}
                                        options={
                                            user.map(v => (
                                                {
                                                    value: v._id,
                                                    label: v.name,
                                                }
                                            ))
                                        } >
                                    </Select>
                                </Form.Item>
                            </Col>

                            <Col span={24}>
                                <Form.Item label="Coupon Type" required>
                                    <Select value={data.typeOfUse} onChange={e => { handleChange({ typeOfUse: e }) }} >
                                        <Select.Option value="Single">Single Use</Select.Option>
                                        <Select.Option value="Multiple">Multiple Use</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Status" required>
                                    <Select value={data.status} onChange={e => { handleChange({ status: e }) }} >
                                        <Select.Option value={true}>Active</Select.Option>
                                        <Select.Option value={false}>Inactive</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});












