import { Button, Form, Input, Row, Col, Popconfirm, Modal, Spin, Typography, Tabs, Card } from 'antd';
import { PlusOutlined, DeleteOutlined, LoadingOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle } from 'react';
import service from '../../services/application';
import { AntdMsg } from '../../utils/Antd';
import TinyMce from "../../utils/TinyMce"

export default function Application() {

    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [currentTab, setCurrentTab] = useState(0);

    const handleChange = (value, changeForm) => {
        if (changeForm) {
            Object.entries(value).forEach(ent => {
                let varDt = data;
                const k = ent[0];
                const v = ent[1];
                let keys = k.split('.');
                for (let i = 0; i < keys.length; i++) {
                    if (i + 1 === keys.length) {
                        varDt[keys[i]] = v;
                    } else {
                        if (typeof varDt[keys[i]] === 'undefined') {
                            if (parseInt(keys[i + 1]) * 1 >= 0) {
                                varDt[keys[i]] = [];
                            } else {
                                varDt[keys[i]] = {};
                            }
                        } varDt = varDt[keys[i]];
                    }
                }
            });
            setData({ ...data });
        }
    }

    const save = () => {
        setAjxRequesting(true);
        try {
            (() => {
                return currentTab === 0 ? service.save({ ...data, type: 'aboutus', data: data.data }) :
                    currentTab === 1 ? service.save({ ...data, type: 'termcondition', data: data.data }) :
                        currentTab === 2 ? service.save({ ...data, type: 'faq', data: data.data }) :
                            currentTab === 3 ? service.save({ ...data, type: 'contactus', data: data.data }) :
                                currentTab === 4 ? service.save({ ...data, type: 'privacy-policy', data: data.data }) : null

            })().then(res => {
                AntdMsg(res.message);
                handleChange({ ...res.data });
                setCurrentTab((currentTab + 1) % 5);
            }).catch(err => {
                if (typeof err.message === 'object') {
                    let dt = err.message[Object.keys(err.message)[0]];
                    AntdMsg(dt, 'error');
                } else {
                    AntdMsg(err.message, 'error');
                }
            }).finally(() => {
                setAjxRequesting(false);
            });
        } catch (err) {
            AntdMsg(err.message, 'error');
        }
    }

    const getDetails = (data) => {
        setAjxRequesting(true);

        service.detail(data).then(res => {
            setData(res.data || {});
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    useEffect(() => {
        if (currentTab === 0) {
            getDetails({ _id: data._id, type: "aboutus" });
        } else if (currentTab === 1) {
            getDetails({ _id: data._id, type: "termcondition" });
        } else if (currentTab === 2) {
            getDetails({ _id: data._id, type: "faq" });
        } else if (currentTab === 3) {
            getDetails({ _id: data._id, type: "contactus" });
        } else if (currentTab === 4) {
            getDetails({ _id: data._id, type: "privacy-policy" });
        }
    }, [currentTab])



    return (
        <>
            <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                <Tabs activeKey={currentTab} type="card" size="small" style={{ marginBottom: 0 }} onChange={(v) => { setCurrentTab(v); }}
                    items={[
                        {
                            label: "About us",
                            key: 0,
                            children: <Card style={{ borderTop: 0 }}>
                                <Aboutus {...{ data, handleChange, save }} />
                            </Card>,
                        },
                        {
                            label: "Terms & Condition",
                            key: 1,
                            children: <Card style={{ borderTop: 0 }}>
                                <TandC {...{ data, handleChange, save }} />
                            </Card>,
                        },
                        {
                            label: "FAQ",
                            key: 2,
                            children: <Card style={{ borderTop: 0 }}>
                                <FAQ {...{ data, handleChange, save }} />
                            </Card>,
                        },
                        {
                            label: "Contact us",
                            key: 3,
                            children: <Card style={{ borderTop: 0 }}>
                                <Contactus {...{ data, handleChange, save }} />
                            </Card>,
                        },
                        {
                            label: "Privacy Policy",
                            key: 4,
                            children: <Card style={{ borderTop: 0 }}>
                                <PrivacyPolicy {...{ data, handleChange, save }} />
                            </Card>,
                        }
                    ]}
                />
                <div style={{ display: "flex", flexDirection: "row-reverse", paddingTop: "5px", paddingRight: "70px" }}>
                    <Button key="save" type="primary" onClick={save}>{currentTab === 4 ? "Save" : "Next"}</Button>
                </div>
            </Spin>

        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, data }) {

    const [modelNames, setModelNames] = useState([])

    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined) {
            qData[key] = "";
            list()
        }
        
    }


    useEffect(() => {
        const models = data?.map((item) => (
            {
                label: item.modelName,
                value: item.modelId,
            }

        ))
        setModelNames(models)

    }, [data])




    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected dealer?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [currentTab, setCurrentTab] = useState(0);

    const handleChange = (value) => {

        Object.entries(value).forEach(ent => {
            let varDt = data;
            const k = ent[0];
            const v = ent[1];
            let keys = k.split('.');
            for (let i = 0; i < keys.length; i++) {
                if (i + 1 === keys.length) {
                    varDt[keys[i]] = v;
                } else {
                    if (typeof varDt[keys[i]] === 'undefined') {
                        if (parseInt(keys[i + 1]) * 1 >= 0) {
                            varDt[keys[i]] = [];
                        } else {
                            varDt[keys[i]] = {};
                        }
                    } varDt = varDt[keys[i]];
                }
            }
        });
        setData({ ...data });
    }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : { status: true }
            );
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        service.detail(data._id).then(res => {
            setData(res.data || {});
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        setAjxRequesting(true);

        try {
            (() => {
                return currentTab === 0 ? service.save({ type: 'aboutus', data: data.data }) :
                    currentTab === 1 ? service.save({ type: 'termcondition', data: data.data }) :
                        currentTab === 2 ? service.save({ type: 'faq', data: data.data }) :
                            currentTab === 3 ? service.save({ type: 'contactus', data: data.data }) :
                                currentTab === 4 ? service.save({ type: 'privacy-policy', data: data.data }) : null

            })().then(res => {
                AntdMsg(res.message);
                handleChange({ ...res.data });
                setCurrentTab((currentTab + 1) % 5);
                list();
            }).catch(err => {
                if (typeof err.message === 'object') {
                    let dt = err.message[Object.keys(err.message)[0]];
                    AntdMsg(dt, 'error');
                } else {
                    AntdMsg(err.message, 'error');
                }
            }).finally(() => {
                setAjxRequesting(false);
            });
        } catch (err) {
            AntdMsg(err.message, 'error');
        }
    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [currentTab, data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        } else {
            setCurrentTab(0);
        }
    }, [open]);

    useEffect(() => {
        console.log(data)
        if (data.type === "aboutus") {
            setCurrentTab(0)
        } else if (data.type === "termcondition") {
            setCurrentTab(1)
        } else if (data.type === "faq") {
            setCurrentTab(2)
        } else if (currentTab === 3) {
            data.type = "contactus"
        } else if (currentTab === 4) {
            data.type = "privacy-policy"
        }
    }, [currentTab])


    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Data'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                maskClosable={false}
                width={1200}
                className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>{currentTab === 4 ? "Save" : "Next"}</Button>
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Tabs activeKey={currentTab} type="card" size="small" style={{ marginBottom: 0 }} onChange={(v) => { setCurrentTab(v); }}
                        items={[
                            {
                                label: "About us",
                                key: 0,
                                children: <Card style={{ borderTop: 0 }}>
                                    <Aboutus {...{ data, handleChange, save }} />
                                </Card>,
                                disabled: data.type === "aboutus"
                            },
                            {
                                label: "Terms & Condition",
                                key: 1,
                                children: <Card style={{ borderTop: 0 }}>
                                    <TandC {...{ data, handleChange, save }} />
                                </Card>,
                            },
                            {
                                label: "FAQ",
                                key: 2,
                                children: <Card style={{ borderTop: 0 }}>
                                    <FAQ {...{ data, handleChange, save }} />
                                </Card>,
                            },
                            {
                                label: "Contact us",
                                key: 3,
                                children: <Card style={{ borderTop: 0 }}>
                                    <Contactus {...{ data, handleChange, save }} />
                                </Card>,
                            },
                            {
                                label: "Privacy Policy",
                                key: 4,
                                children: <Card style={{ borderTop: 0 }}>
                                    <PrivacyPolicy {...{ data, handleChange, save }} />
                                </Card>,
                            }
                        ]}
                    />
                </Spin>

            </Modal>
        </>
    );
});

const Aboutus = ({ data, handleChange }) => {
    return (
        <>
            <Form layout="vertical">
                <Row gutter={[12, 2]}>
                    <Col span={24}>
                        <Form.Item label="Data" required>
                            <TinyMce initialValue={data.data} onChange={(v) => { data.data = v }} />
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </>
    );
}

