const Contactus = ({ data, handleChange }) => {

    const [editableStr, setEditableStr] = useState('This is an editable text.');
    const [a, setA] = useState([1])
    const [b, setB] = useState([1])

    return (
        <>
            <Form layout="vertical">
                <Row gutter={[12, 2]} style={{ width: "100%", padding: "0 5px" }}>
                    <Col span={6}>

                        {a.map(() => {
                            return <Form.Item label="Email" required>

                                <Typography.Paragraph

                                    style={{ marginTop: "8px" }}
                                    editable={{
                                        onChange: setEditableStr,
                                    }}
                                >
                                    {editableStr}
                                </Typography.Paragraph>

                            </Form.Item>

                        })}
                        <Button onClick={() => setA((pre) => {
                            return [...pre, 1]
                        })}>Add</Button>
                    </Col>
                    <Col span={6}>
                        {b.map(() => {
                            return <Form.Item label="Phone Number" required>

                                <Typography.Paragraph
                                    style={{ marginTop: "8px" }}
                                    editable={{
                                        onChange: setEditableStr,
                                    }}
                                >
                                    {editableStr}
                                </Typography.Paragraph>

                            </Form.Item>
                        })}
                        <Button onClick={() => setB((pre) => {
                            return [...pre, 1]
                        })}>Add</Button>
                    </Col>
                    <Col span={12}>
                        <Form.Item label="Address" required>
                            <Input placeholder="Street No: 9, Delhi: 110054" value={data.data?.address} onChange={e => { handleChange({ data: { ...data.data, address: e.target.value } }) }} />
                        </Form.Item>

                    </Col>
                </Row>
            </Form>
        </>
    );
}
const PrivacyPolicy = ({ data, handleChange }) => {


    return (
        <>
            <Form layout="vertical">
                <Row gutter={[12, 2]}>
                    <Col span={24}>
                        <Form.Item label="Data" required>
                            <TinyMce initialValue={data.data} onChange={(v) => { data.data = v }} />

                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </>
    );
}