/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Input, Tag } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import Pagination from '../components/Pagination';
import service from '../../services/category';
import util from '../../utils/util';
import { AntdMsg } from '../../utils/Antd';


export default function Category({ parentId }) {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, parentId });
    const addNewModalRef = useRef();
    const addSubNewModalRef = useRef();


    const columns = [
        {
            title: '#',
            width: 25,
            render: (v, row, i) => <b>{i + 1}</b>
        },
        {
            title: 'Name',
            dataIndex: 'name',
            width: 200,
        },
        {
            title: 'Lable',
            dataIndex: 'lable',
            width: 100,
            render: (v, row) => {
                return <Button onClick={() => { addSubNewModalRef.current.openForm(row) }} > Detail</Button>
            }
        },
        {
            title: 'Slug',
            dataIndex: 'slug',
            width: 100,
        },

        {
            title: 'Status',
            dataIndex: 'status',
            width: 80,
            render: isActive => {
                if (isActive) {
                    return <Tag color='green'>Active</Tag>
                } else {
                    return <Tag color='red'>Inactive</Tag>
                }
            },
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this data?"
                        onConfirm={() => { deleteData(row._id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        setLoading(true);
        service.list(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    const deleteData = (id) => {
        service.delete(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };



    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);



    return (
        <>
            {
                !parentId
                    ? <Typography.Title level={5} style={{ marginTop: 0 }}>List of Product</Typography.Title>
                    : null
            }

            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list, parentId }} />
            <NewForm ref={addSubNewModalRef} ></NewForm>
        </>
    );
}

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, }) {

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col
                    xs={{ span: 24 }}
                    sm={{ span: 16 }}
                    md={{ span: 10 }}
                    lg={{ span: 4 }}

                >
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name or code" value={qData.key} onChange={e => (setQData({ ...qData, key: e.target.value }))} allowClear />
                    </Form.Item>
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected data?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }
                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list, parentId } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({});
    const [ajxRequesting, setAjxRequesting] = useState(false);


    const handleChange = (value) => {
        setData({ ...data, ...value });
    }


    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            console.log('dt', dt);
            setData(
                dt
                    ? { _id: dt }
                    : { parentId, isTop: false }
            );
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        service.details(data._id).then(res => {
            setData(res.data || {});
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        setAjxRequesting(true);
        service.save(data).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            list();
            setOpen(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });

    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        }
    }, [open]);

    useEffect(() => {
        if (!data._id) {
            handleChange({ slug: util.removeSpecialChars(data.name || '') });
        }
    }, [data.name]);


    return (
        <>

            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Product'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={500}
                className="app-modal-body"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>save</Button>,
                ]}
            >


                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={24}>
                                <Form.Item label="Name" required>
                                    <Input placeholder="Name" value={data.name} onChange={e => { handleChange({ name: e.target.value }) }} />
                                </Form.Item>
                            </Col>

                            <Col span={24}>
                                <Form.Item label="is Top" required>
                                    <Select value={data.isTop} onChange={e => { handleChange({ isTop: e }) }} >
                                        <Select.Option value={true}>Yes</Select.Option>
                                        <Select.Option value={false}>No</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Status" required>
                                    <Select value={data.status} onChange={e => { handleChange({ status: e }) }} >
                                        <Select.Option value={true}>Active</Select.Option>
                                        <Select.Option value={false}>Inactive</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});





const NewForm = forwardRef((props, ref) => {
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({});

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData({ ...dt });
        }
    }));



    return (
        <>

            <Modal
                title={<>Data of &nbsp;<span style={{ color: 'red' }}>{data.name}</span></>}
                style={{ top: 20 }}
                open={open}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={1200}
                className="app-modal-body"
                footer={null}
            >
                <Category parentId={data._id}></Category>
            </Modal>
        </>
    );
});





