/* eslint-disable react-hooks/exhaustive-deps */

import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Input, Tag, Divider } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import Pagination from '../../components/Pagination';
import { AntdMsg } from '../../../utils/Antd';
import service from '../../../services/product';
import util from '../../../utils/util';
import Varient from './Varient';
import UploadImage from '../../../utils/UploadImage';
import categoryService from '../../../services/category';

export default function Product({ parentId }) {
    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [category, setCategory] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, parentId });
    const addNewModalRef = useRef();
    const addNewLabelRef = useRef();
    const columns = [
        {
            title: '#',
            width: 40,
            render: (v, row, i) => <b>{i + 1}</b>
        },
        {
            title: 'Name',
            dataIndex: 'name',
            width: 150,
        },
        {
            title: 'Slug',
            dataIndex: 'slug',
            width: 100,
        },
        {
            title: 'Description',
            dataIndex: 'description',
            width: 400,
            render: (v) => <Typography.Text ellipsis={{ expandable: true }}>{v}</Typography.Text>,
        },
        {
            title: 'Meta-Title',
            dataIndex: 'metaTitle',
            width: 100,
        },
        {
            title: 'Meta-Tag',
            dataIndex: 'metaTag',
            width: 100,
        },
        {
            title: 'Meta-Description',
            dataIndex: 'metaDescription',
            width: 100,
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 80,
            render: isActive => {
                if (isActive) {
                    return <Tag color='green'>Active</Tag>
                } else {
                    return <Tag color='red'>Inactive</Tag>
                }
            },
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this data?"
                        onConfirm={() => { deleteData(row._id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);
    function list() {
        setLoading(true);
        service.listUser(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    function listCategory() {
        categoryService.list()
            .then(res => { setCategory(res.data || []); }).catch(err => { AntdMsg('Product data not loaded', 'error'); });
    }

    const deleteData = (id) => {
        service.deleteUser(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }
    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));
    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';
    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };
    useEffect(() => {
        list();
        listCategory();
    }, [qData.page, qData.limit]);
    return (
        <>
            {!parentId ? <Typography.Title level={5} style={{ marginTop: 0 }}>Product List</Typography.Title> : null}
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list, parentId, category }} />
            <AddLabel ref={addNewLabelRef} {...{ list }} />
        </>
    );
};
function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, }) {
    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col xs={24} xl={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name or code" value={qData.key} onChange={e => (setQData({ ...qData, key: e.target.value }))} allowClear />
                    </Form.Item>
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected data ?"
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }
                </Col>
            </Row>
        </Form>
    );
};
const AddForm = forwardRef((props, ref) => {
    const { list, parentId, category } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [thumbFile, setThumbFile] = useState([]);
    const [file, setFile] = useState([]);


    const handleChange = (v, k) => {
        let varDt = data;
        let keys = k.split('.');
        for (let i = 0; i < keys.length; i++) {
            if (i + 1 === keys.length) {
                varDt[keys[i]] = v;
            } else {
                if (typeof varDt[keys[i]] === 'undefined') {
                    if (parseInt(keys[i + 1]) * 1 >= 0) {
                        varDt[keys[i]] = [];
                    } else {
                        varDt[keys[i]] = {};
                    }
                } varDt = varDt[keys[i]];
            }
        }
        setData({ ...data });
    }

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : { parentId, isPremium: false, isBestSelling: false, forBirthday: false, forAnniversary: false, isBestSellingCombos: false, variants: [{}] }
            );
        }
    }));
    console.log(data);
    const getDetails = () => {
        setAjxRequesting(true);
        service.detailUser(data._id).then(res => {
            setData(res.data || {});
            setFile(res.data.imgs.length ? [...res.data.imgs] : [])
            setThumbFile(res.data.thumbImg ? [res.data.thumbImg] : [])
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }
    const save = () => {
        setAjxRequesting(true);
        service.saveUser(data).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            list();
            setOpen(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });
    }
    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [data._id]);

    useEffect(() => {
        if (!data._id) {
            handleChange(util.removeSpecialChars(data.name || ""), 'slug');
        }
    }, [data.name]);

    useEffect(() => {
        if (!data._id) {
            handleChange(util.removeSpecialChars(data?.name || ''), 'slug');
        }
    }, [data.name]);

    useEffect(() => {
        setData({ ...data, thumbImg: thumbFile[0] })
    }, [thumbFile])

    useEffect(() => {
        setData({ ...data, imgs: file })
    }, [file])

    useEffect(() => {
        if (!open) {
            setData({})
        }
    }, [open])

    return (
        <>
            <Modal
                title={(!data._id ? 'Add New' : 'Edit') + ' Product'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={1000}
                className="app-modal-body"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >

                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={12}>
                                <Form.Item label="Name" required>
                                    <Input placeholder="Name" value={data.name} onChange={e => { handleChange(e.target.value, "name") }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Slug" required>
                                    <Input placeholder="Slug" value={data.slug} onChange={e => { handleChange(util.removeSpecialChars(e.target.value), "slug") }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Description" required>
                                    <Input.TextArea rows={4} placeholder="Description" value={data.description} onChange={e => { handleChange(util.removeSpecialChars(e.target.value), "description") }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Meta-Title" required>
                                    <Input placeholder="Meta-Title" value={data.metaTitle} onChange={e => { handleChange(e.target.value, "metaTitle") }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Category" required>
                                    <Select mode="multiple" value={data.categoryIds} onChange={v => { handleChange(v, "categoryIds") }}
                                        options={
                                            category.map(v => (
                                                {
                                                    value: v._id,
                                                    label: v.name,
                                                }
                                            ))
                                        } >
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Meta-Tag" required>
                                    <Input placeholder="Meta-Tag" value={data.metaTag} onChange={e => { handleChange(e.target.value, "metaTag") }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Meta-Description" required>
                                    <Input.TextArea rows={4} placeholder="Meta-Description" value={data.metaDescription} onChange={e => { handleChange(e.target.value, "metaDescription") }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="High-Light" required>
                                    <Input.TextArea rows={4} placeholder="High-Light..." value={data.heighlight} onChange={e => { handleChange(e.target.value, "heighlight") }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Premium" required>
                                    <Select value={data.isPremium} onChange={e => { handleChange(e, "isPremium") }} >
                                        <Select.Option value={true}>Yes</Select.Option>
                                        <Select.Option value={false}>No</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Best Selling" required>
                                    <Select value={data.isBestSelling} onChange={e => { handleChange(e, "isBestSelling") }} >
                                        <Select.Option value={true}>Yes</Select.Option>
                                        <Select.Option value={false}>No</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Best Selling Combos" required>
                                    <Select value={data.isBestSellingCombos} onChange={e => { handleChange(e, "isBestSellingCombos") }} >
                                        <Select.Option value={true}>Yes</Select.Option>
                                        <Select.Option value={false}>No</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="BirthDay" required>
                                    <Select value={data.forBirthday} onChange={e => { handleChange(e, "forBirthday") }} >
                                        <Select.Option value={true}>Yes</Select.Option>
                                        <Select.Option value={false}>No</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Anniversary" required>
                                    <Select value={data.forAnniversary} onChange={e => { handleChange(e, "forAnniversary") }} >
                                        <Select.Option value={true}>Yes</Select.Option>
                                        <Select.Option value={false}>No</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Status" required>
                                    <Select value={data.status} onChange={e => { handleChange(e, "status") }} >
                                        <Select.Option value={true}>Active</Select.Option>
                                        <Select.Option value={false}>Inactive</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={24}>

                                <Variant {...{ variantDetails: data.variants, parentKay: 'variants', handleChange }} />
                            </Col>
                            <Divider />
                            <br />
                            <Col span={4}>
                                <Form.Item label="Thumbnail Photo">
                                    <UploadImage {...{ fileList: thumbFile, setFileList: setThumbFile }} />
                                </Form.Item>
                            </Col>
                            <Col span={20}>
                                <Form.Item label="Photo">
                                    <UploadImage {...{ fileList: file, setFileList: setFile, count: 20 }} />
                                </Form.Item>
                            </Col>

                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});

const AddLabel = forwardRef((props, ref) => {
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData({ ...dt });
        }
    }));

    return (
        <>
            <Modal
                title={(!data._id ? `Add User of ${(data.name)}` : `Variants of ${(data.name)}`)}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={1200}
                className="app-modal-body"
            >
                <Varient productId={data._id} />
            </Modal>
        </>
    );
});


function Variant({ variantDetails: data, parentKay, handleChange }) {
    useEffect(() => {
        console.log(parentKay, parentKay === 'variants');
    }, [parentKay])
    return (
        <>
            <div><Divider orientation="left">{parentKay === 'variants' ? "Variants" : "Variant"}</Divider></div>
            <Row>
                <Col span={4}>
                    <label className="req">Weight</label>
                </Col>
                <Col span={4}>
                    <label className="req">Price</label>
                </Col>
                <Col span={8}>
                    <label className="req">Actual Price (In )</label>
                </Col>
            </Row>

            {
                data?.map((v, i) => (
                    <React.Fragment key={i}>
                        <Row>
                            <Col span={4}>
                                <Input placeholder="Weight" value={v.weight} onChange={(e) => { handleChange((e.target.value), `${parentKay}.${i}.weight`) }} />

                            </Col>
                            <Col span={4}>

                                <Input placeholder="Price" value={v.price} onChange={(e) => { handleChange(util.handleInteger(e.target.value), `${parentKay}.${i}.price`) }} />
                            </Col>
                            <Col span={8}>

                                <Input placeholder="Actual Price" value={v.actualPrice} onChange={(e) => { handleChange(util.handleFloat(e.target.value), `${parentKay}.${i}.actualPrice`) }} />
                            </Col>
                            <Col span={8}>
                                {
                                    i + 1 === data.length
                                        ? <Button type="dashed" onClick={() => {
                                            handleChange([...data, {}], parentKay);
                                        }}>Add</Button>
                                        : null
                                }
                                {
                                    i + 1 === data.length && i !== 0
                                        ? <Button type="dashed" className="mx-2" danger onClick={() => {
                                            data.pop();
                                            handleChange([...data], parentKay);
                                        }}>Remove</Button>
                                        : null
                                }
                            </Col>
                        </Row>

                    </React.Fragment>
                ))
            }
        </>
    );
}